import React, { useContext } from 'react'
import ItemMenu from '../components/ItemMenu'
import invoke from '../utils/invoke'
import { MakeContext } from '../routes/MakeContext'

export default function FullScreenItemMenu (props) {
  const make = useContext(MakeContext)
  function handleSelect (item) {
    invoke('closeItemMenu')
    // invoke('log', 'pppppp' + JSON.stringify(item))
    // invoke('itemMakeModal',item)
    // if (item.colors) {
    //   make.setItemSelected(item.id)
    //   make.setItemSelectedColor('')
    // } else {
    //   make.setItemSelectedColor(item.id)
    //   // make.setItemSelected('')
    // }
    if (!item.colors) {
      make.setItemSelectedColor(item.id)
    }
  }

  function closeMenu () {
    invoke('closeItemMenu')
  }

  return (
    <ItemMenu onSelect={(item) => handleSelect(item)} closeMenu={() => closeMenu()} />
  )
}
